import {
	Box,
	Checkbox,
	List,
	ListItem,
	ListItemAvatar,
	ListItemText,
} from '@material-ui/core';
import Icon from '@material-ui/core/Icon'
import { useCallback, useEffect, useState } from 'react';
import { Category } from '../../../models';
import axios from 'axios';

//const CategoryList = Object.values(Category);

interface Props {
	items: Category[];
	onChange(values: Category[]): any;
}
interface Cat {
	name?: string | undefined;
	icon?: string | undefined;
	items?: string[];
}
export function CategoryPicker(props: Props) {

	//  const Categories = [
	// 	{
	// 		name: 'Comedy',
	// 		icon: 'tag_faces',
	// 		items : ["Humour & Funny","Stand-up Comedians","Sketch Comedy"]
	// 	},
	// 	{
	// 		name: 'Lifestyle',
	// 		icon: 'nightlife',
	// 		items : ["Budget Lifestyle","Luxury Lifestyle"]
	// 	},
	
	// ];

	const [Categories, setCategories] = useState<Cat[]>([]);

	useEffect(()=>{
		axios.get('/api/campaign/categories').then(res => {
			setCategories(res.data);
		})
	},[])
	const onChecked = useCallback(
		(category: Category, check: boolean) => {
			let next = [...props.items];
			if (check) {
				if (!next.includes(category)) {
					next.push(category);
				}
			} else {
				next = next.filter(c => c !== category);
			}
			props.onChange(next);
		},
		[props]
	);

	const onCheckedMain = useCallback(
		(category: string[], check: boolean) => {
			let next = [...props.items];

			
				category.forEach(s=> {
					if (check) {
					if (!next.includes(s as Category)) {
						next.push(s as Category);
					}
				} else {
					next = next.filter(c => c !== s as Category);
				}
				})
			
			props.onChange(next);
		},
		[props]
	);

	return (
		<Box minWidth='320px'>
			<List dense disablePadding>
				{Categories.length > 0 && Categories.map(c => {
					let mainchecked = true;
					// @ts-ignore: Object is possibly 'null'.
					c.items.forEach(s=> {
						if(!props.items.includes(s as Category)) {
							mainchecked= false;
						}
					})
					return (
						<div style={{display: 'flex', flexDirection:'row'}}>
						<ListItem dense button key={c.name}>
							<ListItemAvatar>
								<div style={{display: 'flex'}}>
								
								<Checkbox
									color='primary'
									size='small'
									checked={mainchecked}
									onChange={() => onCheckedMain(c.items?? [], !mainchecked)}
								/>
								<Icon color='primary' style={{margin: '7px', fill: "green"}}>{c.icon}</Icon>
								  
								</div>
							</ListItemAvatar>
							<ListItemText primary={c.name} />
							
							<div>
							
							<List dense disablePadding>
							 {/* @ts-ignore: Object is possibly 'null'. */}
							{c.items.map(item => {
								let checked = props.items.includes(item as Category);
					return (
						<div >
							
						<ListItem dense button key={item}>
							<ListItemAvatar>
								<div >
								
								<Checkbox
									color='primary'
									size='small'
									checked={checked}
									onChange={() => onChecked(item  as Category, !checked)}
								/>
								  
								</div>
							</ListItemAvatar>
							<ListItemText primary={item} />
						</ListItem>
						
						</div>
						
						
					);
				})}
				</List>
				</div>
						</ListItem>
						</div>
						
					);
				})}
			</List>
		</Box>
	);
}
